/* LIBS */
/* Slider */
@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400&subset=cyrillic");
@import url("https://fonts.googleapis.com/css?family=Roboto:700&subset=cyrillic");
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* HELPERS */
.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: "Bebas-Neue-Bold";
  src: url("../fonts/Bebas-Neue-Bold.woff2") format("woff2"), url("../fonts/Bebas-Neue-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Constantia";
  src: url("../fonts/Constantia.woff2") format("woff2"), url("../fonts/Constantia.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* COMPONENTS */
body {
  min-width: 320px;
  font-family: 'Open Sans', Arial, sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.2;
  color: #262423;
}

body.is-locked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  overflow: hidden;
}

* {
  -webkit-tap-highlight-color: transparent;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

button {
  background-color: transparent;
}

p::selection, a::selection, span::selection, div::selection, img::selection, mark::selection {
  color: #fff;
  background-color: #7b232c;
}

img {
  vertical-align: top;
  max-width: 100%;
}

::-ms-clear,
::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input:invalid {
  box-shadow: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

video::-webkit-media-controls-start-playback-button {
  display: none !important;
}

.obj-fit {
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover;';
}

.container {
  max-width: 1800px;
  margin: 0 auto;
  padding: 0 15px;
}

.title {
  font-family: "Constantia", Arial, sans-serif;
  font-weight: normal;
  color: #7b232c;
}

.title_md {
  font-size: 42px;
}

.title_lg {
  font-size: 67px;
  line-height: 89px;
}

.title-decor {
  position: relative;
  padding: 4px 0;
  font-family: "Constantia", Arial, sans-serif;
  font-weight: normal;
  font-size: 36px;
  color: #7b232c;
  text-align: center;
  border-top: 3px solid #7b232c;
  border-bottom: 3px solid #7b232c;
}

.title-decor::after {
  content: '';
  position: absolute;
  top: -45px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  width: 109px;
  height: 40px;
  background: url("../img/bg/big-title-decor.png") no-repeat 50% 50%;
}

.title-decor span {
  display: inline-block;
  vertical-align: middle;
  padding: 5px 0;
  border-top: 1px solid #725b55;
  border-bottom: 1px solid #725b55;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 0 39px;
  border-radius: 10px;
  transition: background-color .2s;
}

.btn_red {
  height: 38px;
  color: #fff;
  background-color: #7b232c;
  font-size: 17px;
  line-height: 37px;
  font-family: 'Open Sans', Arial, sans-serif;
  font-weight: 400;
}

.no-touch .btn_red:hover {
  background-color: #671d25;
}

.header__in {
  padding: 20px 0;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__title-decor {
  flex-basis: 522px;
}

.address {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-family: 'Open Sans', Arial, sans-serif;
  font-weight: 400;
}

.address__item {
  position: relative;
  padding-left: 30px;
}

.address__item::before {
  content: '';
  position: absolute;
  top: 8px;
  left: 0;
}

.address__item_link {
  font-size: 20px;
  line-height: 44px;
}

.address__item_link::before {
  background-image: url("../img/sprite.png");
  width: 23px;
  height: 28px;
  background-position: -49px 0px;
}

.address__item_link span {
  border-bottom: 2px dashed #262423;
  transition: .2s;
}

.no-touch .address__item_link span:hover {
  border-bottom-color: transparent;
}

.address__item_tel {
  font-size: 24px;
  line-height: 39px;
}

.address__item_tel::before {
  background-image: url("../img/sprite.png");
  width: 24px;
  height: 26px;
  background-position: -82px 0px;
}

.address__btn {
  align-self: flex-end;
}

/* ===================================
	 nav
	 @mixin ================================== */
.nav {
  border-top: 2px solid #7b232c;
  background-color: #f0d4aa;
}

.nav__list {
  position: relative;
  padding: 17px 0;
  text-align: center;
  font-size: 0;
}

.nav__list::before, .nav__list::after {
  content: '';
  position: absolute;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.nav__list::before {
  left: 10%;
  width: 100px;
  height: 24px;
  background-image: url("../img/bg/nav-decor_l.png");
}

.nav__list::after {
  right: 10%;
  width: 115px;
  height: 24px;
  background-image: url("../img/bg/nav-decor_r.png");
}

.nav__list-item {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  line-height: 1;
}

.nav__list-item::after {
  content: '';
  position: absolute;
  top: -10px;
  right: 0;
  width: 1px;
  height: 40px;
  background: rgba(123, 35, 44, 0.2);
}

.nav__list-item.is-active .nav__list-link {
  color: #7b232c;
  border-bottom-color: #7b232c;
}

.nav__list-link {
  display: block;
  font-size: 20px;
  border-bottom: 1px solid transparent;
  transition: all .25s ease;
}

.no-touch .nav__list-link:hover {
  color: #7b232c;
}

